@import "mixins";

@import "plugins/bootstrap";
@import "plugins/font-awesome";
@import "plugins/animate";


.has-error {
    .mce-panel {
        border-color: $state-danger-text;
    }
}

// tinymce
.mce-tinymce {
    border-radius: $border-radius-base $border-radius-base 0 0;
}

.disabled {
    .mce-tinymce {
        cursor: progress;
        @include opacity(.5);
        pointer-events: none;
    }
}

.mce-panel {
    border: none !important;

    iframe {
        border-radius: 0 0 $border-radius-base $border-radius-base;
    }
}
.mce-toolbar {
    background: #B3B3B3 !important;
    border-radius: $border-radius-base $border-radius-base 0 0;
}
.mce-btn {
    border: 0 !important;

    &.mce-active {
        button {
            background: darken(#B3B3B3, 10%) !important;
        }
    }

    button {
        background: #B3B3B3 !important;
        color: #fff !important;

        &:hover, &:active {
            background: darken(#B3B3B3, 5%) !important;
        }
    }

    i {
        text-shadow: none !important;
    }
}
.mce-ico {
    color: #fff !important;
}

// @todo Move this into `modules/fileapi.scss`
.jcrop-keymgr {
    display: none !important;
}

.jcrop-holder {
    margin: 0 auto;
}

// @todo Move this into `modules/notifications.scss`
.cg-notify-message {
    @extend .alert;
    max-width: 400px;
    position: fixed;
    right: 5px;
    @include transition(top .5s ease-out,opacity .2s ease-out);
    z-index: 1050;

    .cg-notify-message-template {
        img {
            max-width: 60px;

            &.pull-left {
                margin-bottom: 10px;
                margin-right: 10px;
            }
        }
    }
    .cg-notify-close {
        @extend .close;
        color: inherit;
        position: absolute;
        right: 5px;
        top: 0;
        z-index: 1;
    }

    .cg-notify-sr-only {
        @extend .sr-only;
    }
}



.ng-table-pager {
    display: none;
}
